import type { FC } from "react";
import { INSTRUMENTS, type Pattern } from "@/types/sequencer";

type Props = {
  pattern: Pattern;
  currentStep: number;
  onToggleStep: (row: number, col: number) => void;
};

export const PatternGrid: FC<Props> = ({
  pattern,
  currentStep,
  onToggleStep,
}) => {
  return (
    <section className="mt-3 flex flex-col gap-2">
      <div className="font-mono text-[11px] uppercase tracking-[0.2em] text-neutral-500">
        Pattern
      </div>
      <div className="overflow-x-auto">
        <div className="inline-grid grid-cols-[80px_repeat(16,minmax(24px,1fr))] border border-neutral-300 bg-white">
          <div className="border-r border-neutral-300 bg-neutral-100" />
          {pattern[0]?.map((_, col) => {
            const isCurrent = col === currentStep;
            return (
              <div
                key={col}
                className={`flex h-7 items-center justify-center border-l border-neutral-200 text-[9px] font-mono text-neutral-400 ${
                  isCurrent ? "bg-emerald-50" : ""
                }`}
              >
                {col + 1}
              </div>
            );
          })}

          {INSTRUMENTS.map((instrument, row) => (
            <Row
              key={instrument}
              label={instrument}
              rowIndex={row}
              rowSteps={pattern[row]}
              currentStep={currentStep}
              onToggleStep={onToggleStep}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

type RowProps = {
  label: string;
  rowIndex: number;
  rowSteps: Pattern[number];
  currentStep: number;
  onToggleStep: (row: number, col: number) => void;
};

const Row: FC<RowProps> = ({
  label,
  rowIndex,
  rowSteps,
  currentStep,
  onToggleStep,
}) => {
  return (
    <>
      <div className="flex h-9 items-center justify-end border-t border-r border-neutral-200 bg-neutral-100 px-2 text-[11px] capitalize text-neutral-600">
        {label}
      </div>
      {rowSteps.map((step, col) => {
        const isCurrent = col === currentStep;
        const isActive = step.active;
        return (
          <button
            key={col}
            type="button"
            onClick={() => onToggleStep(rowIndex, col)}
            className={`h-9 border-t border-l border-neutral-200 transition ${
              isActive
                ? "bg-emerald-500/10 text-emerald-700"
                : "bg-white hover:bg-neutral-50"
            } ${isCurrent ? "ring-1 ring-emerald-500/70" : ""}`}
          >
            {isActive && (
              <span className="block text-xs leading-none text-emerald-600">
                ×
              </span>
            )}
          </button>
        );
      })}
    </>
  );
};
